.aida {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: PT Serif;
  border-radius: 2rem;
  top: 3rem;

  .attention {
    background: linear-gradient(0deg, #91ade9, #faf7f5);
    box-shadow: 0 0 0.2rem #ffffff;
    border-radius: 0.3rem;

    .title {
      text-align: center;
      padding: 0.3rem;
      font-size: 1.4rem;
      background-color: #fcfcfc;
    }
    .text {
      width: 90vw;
      text-align: center;
    }
  }

  .interest {
    margin: 3rem 1rem 2rem 2rem;
    background-color: #f7f7f7;
    border-radius: 0.3rem;
    padding: 1rem;

    .title {
      text-align: start;
      font-size: 1.4rem;
      padding: 0.6rem;
      text-transform: uppercase;
    }
    .text {
      margin-top: 2rem;
      text-align: start;

      .icon {
        color: green;
        font-size: 1.2rem;
      }
    }
  }

  .desire {
    width: 70vw;
    text-align: center;
    margin: 3rem;
  }

  .subicon {
    font-size: 1.7rem;
    color: #333533;
  }
  .action {
    text-align: center;
    background-color: rgb(197, 192, 192);
    
    border-radius: 0.5rem;
    padding: 2rem;
    font-size: 1.1rem;
    text-transform: uppercase;
  }

  .button {
    text-decoration: none;
    margin: 2rem;
    font-size: 1.1rem;
    color: #333533;
    text-transform: uppercase;
    background-color: #f3f0f0;
    padding: 1rem 2rem 1rem 2rem;
    filter: drop-shadow(0 0 0.1rem #222121);

    &:hover {
      color: #000000;
      background: linear-gradient(0deg, #91ade9, #faf7f5);
    }
  }
}
