.welcome {
  margin-top: 4rem;
  font-family: PT Serif;
  display: flex;
  flex-direction: column;
  align-items: flex-end;

  .container {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-end;
    height: 30rem;
    right: 0;
    background: linear-gradient(90deg, transparent, white);

    .greeting,
    .intro,
    .instructions {
      width: 50vw;
      text-align: end;
    }

    .greeting {
      font-size: 1.3rem;
    }

    .intro {
      font-size: 1rem;
    }

    .instructions {
      text-align: end;
      border-radius: 0.3rem;
      font-size: 1rem;
    }
  }

  .salon {
    position: relative;
    clip-path: polygon(3% 0, 100% 0, 100% 100%, 3% 100%);
    right: 0;
    height: 30rem;
    z-index: -1;
  }
}
