.table {
  font-family: "PT Serif";
  width: 100%;
  margin: 3rem 3vw 3rem 3vw;
  text-align: center;
  padding: 0.5rem;
  border-top: 0.15rem solid #ababad;
  border-bottom: 0.15rem solid #ababad;
  background: linear-gradient(0deg, #dff2f8, #c9d5ff, #dff2f8);

  .day {
    color: #19408a;
    text-transform: uppercase;
    padding: 0.8rem;
  }

  .hours {
    color: #19408a;
    text-transform: uppercase;
  }
}
