.form {
  font-family: PT Serif;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-top: 2rem;

  .title {
    font-size: 1.3rem;
    text-align: center;
    text-transform: uppercase;
    padding: 0.3rem;
    filter: drop-shadow(0 0.1rem 0.1rem #949090);
    width: 100vw;
    margin-bottom: 2.5rem;
    background: linear-gradient(0deg, #c6cbcc, #c9d5ff, #dff2f8);
  }

  .name {
    text-align: start;

    span.error {
      color: red;
    }
  }

  input[type="date"],
  input[type="text"],
  input[type="tel"],
  select {
    width: 50vw;
    padding: 0.5rem;
    border: 0.1rem solid #949497;
    border-radius: 5px;
    font-size: 1rem;
    margin-right: 8vw;
  }

  button {
    padding: 0.6rem;
    background-color: #007bff;
    color: #fff;
    border: none;
    margin-right: 8vw;
    border-radius: 0.7rem;
    cursor: pointer;
    font-size: 1.3rem;
    transition: background-color 0.3s ease;

    &:disabled {
      background-color: #ccc;
      cursor: not-allowed;
    }

    &:hover:enabled {
      background-color: #0056b3;
    }
  }
}
