.brands {
  .subtitle {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    font-size: 1.8rem;
    text-align: center;
    background-color: gray;
    color: bisque;

    .model {
        width: 20vw;
    }
  }

  .container {
    display: flex;
    align-items: center;
    flex-wrap: wrap;

    .brand {
      margin: 1rem;
      width: 30vw;

      .logo {
        width: 30vw;
      }
    }
  }
}
