.contact_form {
    font-family: PT Serif;
    margin-top: 3rem;

    .title {
        text-align: center;
        font-size: 1.2rem;
    }
    
    .form {
        display: flex;
        flex-direction: column;
        align-items: center;
        &.checkbox {
            
            .required {
              margin-right: 5px;
              color: red;
            }
          }
        }
        
        .input {
          padding: 0.2rem;
          border: 0.2rem solid #aa9c9c;
          border-radius: 0.4rem;
          font-size: 1rem;
          
          &.message {
            height: 100px;
          }
        }
        
        span {
          color: red;
          font-size: 1rem;
        }
        
        button.send {
          padding: 10px 20px;
          background-color: #007bff;
          color: #fff;
          border: none;
          border-radius: 5px;
          cursor: pointer;
          font-size: 18px;
          transition: background-color 0.3s ease;
          
          &:hover {
            background-color: #0056b3;
          }
          
          &:disabled {
            background-color: #ccc;
            cursor: not-allowed;
          }
        }
      }
    
