.blog {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 2rem;

  .eye {
    position: relative;
    height: 10rem;
    left: 59vw;
    z-index: 1;
    filter: drop-shadow(0 0.1rem 0.2rem #0c0c0c);
  }

  .description {
    position: relative;
    top: -3rem;
    font-family: PT Serif;

    .title {
      text-align: start;
      background: radial-gradient(circle at center, #aac8d1, transparent);
    }
    .text {
      padding: 0.5rem;
    }
  }
}
