.navigation {
  height: max-content;

  .classic {
    display: flex;
    align-items: center;
    .link {
      margin: 0.2rem;

      .button {
        font-family: PT Serif;
        color: #060606;
        border-top: none;
        border-left: none;
        border-right: none;
        background: transparent;
        padding: 0.33rem 1vw 0.33rem 1vw;
        
        &.active {
          border-radius: 0.2rem;
          background: white;
          filter: drop-shadow(0rem 0rem 0.05rem white);
        }

        .icon {
          font-size: 1.6rem;
        }

        .title {
          font-size: 1.2rem;
        }
      }
    }
  }
}
