.process {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: PT Serif;

  .title {
    background: radial-gradient(circle at right, #aac8d1, transparent);
    padding: 4rem;
    margin-top: 2rem;
    font-size: 1.4rem;
    text-align: center;
  }

  .text {
    margin: 0 5vw 1rem 5vw;
    text-align: center;

    .subtitle {
      text-decoration: underline;
    }

    .subtext {
    }
  }

  .endtext {
    background: linear-gradient(45deg, #d2c7c1, transparent);
    font-size: 2rem;
    text-transform: uppercase;
    display: flex;
    align-items: center;

    .art {
      height: 100%;
    }
  }
}
