.location {
  display: flex;
  flex-direction: row;
  align-items: center;

  .map {
    width: 60%;
    height: 30rem;
    border-radius: 0.7rem;
  }

  .adress {
    display: flex;
    flex-direction: column;
    align-items: center;

    .icon {
      font-size: 4rem;
    }

    .text {
      font-size: 1.4rem;
      text-align: center;
    }

    .image {
      border-radius: 0.7rem;
      width: 40vw;
    }
  }
}
